*{
    margin: 0;
    padding: 0;
}
body{
    height: 100vh;
    width: 100%;
    background-color:fuchsia;
}
.Count{
    position: relative;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    height: 300px;
    width: 300px;
    border: 3px solid black;
    margin-left: 500px;
    margin-top: 150px;
    background-color:yellow;
}
.Count button{
    color: white;
    background-color:red;
    border: 5px;
    margin: 5px;
    border-radius: 3px;
    padding: 10px;
    text-align: center;
}